export default function RegisterPage() {
  return (
    <div style={{ padding: "40px", maxWidth: "400px" }}>
      <h1>Register</h1>

      <form>
        <input placeholder="Name" /><br /><br />
        <input placeholder="Email" /><br /><br />
        <input type="password" placeholder="Password" /><br /><br />
        <button>Register</button>
      </form>
    </div>
  );
}
